<template>
  <div class="box">
    <h1>{{text[0]}}</h1>
    <h5>{{text[1]}}</h5>
    <div v-html="text[2]" class="text"></div>
  </div>
</template>

<script>
import indexjs from "./js/indexWeb.js"
export default {
  name: "PageWeb",
  data() {
    return {
      urls: indexjs.urls,
      context:this.$route.query.context,
      text:indexjs.text[this.context]
    }
  },
  created() {
    this.urls=indexjs.urls
    this.context=this.$route.query.context
    this.text=indexjs.text[this.context]
  },
}
</script>

<style scoped>
@import "less/PageWeb.less";
</style>